.container_narbar {
  position: fixed;
  z-index: 50;
  width: 100%;
  height: 60px;
  justify-content: space-between;
  background-color: #3498db;
  display: flex;
  align-items: center;

  .container_narbar_logo {
    width: 250px;
    height: 50px;
    margin-left: 120px;
    align-items: center;
    display: flex;
    // position: absolute;
    img {
      width: 50px;
    }
    p {
      margin-left: 10px;
      font-weight: 700;
      text-transform: uppercase;
      color: #fff;
    }
  }

  .container_narbar_content {
    display: flex;
    margin-right: 40px;
    .container_narbar_content_1 {
      align-items: center;
      ul {
        display: flex;
        padding: 10px 20px;
        li {
          .quanly {
            margin-top: 20px;
            position: absolute;
            width: 220px;
            height: 0;
            overflow: hidden;
            background-color: #3498db;
            ul {
              width: 90%;
              margin-left: 0;
              list-style: none;
              text-decoration: none;
              flex-direction: column;
              li {
                width: 100%;
                font-size: 17px;
                padding: 13px 0;
                margin-left: 0;
              }
            }
          }

          &:hover {
            color: #d63031;
            text-decoration: underline;
          }

          cursor: pointer;
          margin-right: 30px;
          font-weight: 500;
          font-size: 17px;
          list-style: none;
          text-decoration: none;
          color: #fff;
        }
      }
    }
    .container_narbar_content_user {
      cursor: pointer;
      right: 8px;
      position: relative;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background: url(https://scr.vn/wp-content/uploads/2020/07/Avatar-Facebook-tr%E1%BA%AFng.jpg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      object-fit: cover;

      a {
        text-decoration: none;
        color: #fff;
      }

      .container_narbar_content_2 {
        display: none;
        width: 220px;
        height: 320px;
        position: absolute;
        bottom: -330px;
        right: 0;
        background-color: #242526;
        .arrow-up {
          position: absolute;
          right: 10px;
          top: -10px;
          width: 0;
          height: 0;
          border-left: 10px solid transparent;
          border-right: 10px solid transparent;
          border-bottom: 10px solid #242526;
        }

        .container_narbar_content_2_1 {
          margin-top: 2px;
          color: #fff;
          width: 90%;
          margin-left: 5%;
          align-items: center;
          height: 60px;
          display: flex;
          i {
            font-size: 20px;
          }
          p {
            margin-top: 0px;
            margin-left: 10px;
            font-size: 18px;
            text-transform: capitalize;
          }

          &:hover {
            color: #0984e3;
          }
        }
      }
    }

    .container_narbar_content_name {
      margin-top: 10px;
      margin-right: 20px;
      font-weight: 600;
      color: #fff;
      text-transform: uppercase;
    }
  }
}
