.container_register {
  width: 100%;
  height: 100vh;
  background: url("https://images.unsplash.com/photo-1542314831-068cd1dbfeeb?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1170&q=80");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  place-items: center;z-index: 10;
  display: grid;

  .container_register_form {
    transform: translateY(20px);
    border-radius: 8px;

    ::-webkit-input-placeholder {
      color: rgba(255, 255, 255, 0.65);
    }

    ::-webkit-input-placeholder .input-line:focus + ::input-placeholder {
      color: #fff;
    }

    .highlight {
      color: rgba(255, 255, 255, 0.8);
      font-weight: 400;
      cursor: pointer;
      transition: color 0.2s ease;
    }

    .highlight:hover {
      color: #fff;
      transition: color 0.2s ease;
    }

    .input-line:focus {
      outline: none;
      border-color: #fff;
      -webkit-transition: all 0.2s ease;
      transition: all 0.2s ease;
    }

    .ghost-round {
      cursor: pointer;
      width: 20px;
      left: 0;
      position: absolute;
      background: none;
      border: 1px solid rgba(255, 255, 255, 0.65);
      border-radius: 25px;
      color: rgba(255, 255, 255, 0.65);
      -webkit-align-self: flex-end;
      -ms-flex-item-align: end;
      align-self: flex-end;
      font-size: 19px;
      font-size: 1.2rem;
      font-family: roboto;
      font-weight: 300;
      line-height: 2.5em;
     bottom: 25px;
      -webkit-transition: all 0.2s ease;
      transition: all 0.2s ease;
    }

    .ghost-round:hover {
      background: rgba(255, 255, 255, 0.15);
      color: #fff;
      -webkit-transition: all 0.2s ease;
      transition: all 0.2s ease;
    }

    .input-line {
      background: none;
      margin-bottom: 10px;
      line-height: 2.4em;
      color: #fff;
      font-family: roboto;
      font-weight: 300;
      letter-spacing: 0px;
      letter-spacing: 0.02rem;
      font-size: 19px;
      font-size: 1.2rem;
      border-bottom: 1px solid rgba(255, 255, 255, 0.65);
      -webkit-transition: all 0.2s ease;
      transition: all 0.2s ease;
    }

    .full-width {
      width: 100%;
    }

    .input-fields {
      margin-top: 25px;
    }

    .container {
      display: -webkit-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
      -webkit-align-items: center;
      -ms-flex-align: center;
      align-items: center;
      -webkit-box-pack: center;
      -webkit-justify-content: center;
      -ms-flex-pack: center;
      justify-content: center;
      background: #eee;
      height: 100%;
    }

    .container_register_form_2 {
      padding-left: 25px;
      padding-right: 25px;
      display: -webkit-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      -webkit-flex-flow: column;
      -ms-flex-flow: column;
      flex-flow: column;
      z-index: 5;
    }

    .welcome {
      font-weight: 200;
      margin-top: 25px;
      text-align: center;
      font-size: 40px;
      font-size: 2.5rem;
      letter-spacing: 0px;
      letter-spacing: 0.05rem;
    }

    .subtitle {
      text-align: center;
      line-height: 1em;
      font-weight: 100;
      letter-spacing: 0px;
      letter-spacing: 0.02rem;
    }

    .menu {
      background: rgba(0, 0, 0, 0.2);
      width: 100%;
      height: 50px;
    }

    .container_register_form_1 {
      z-index: 100;
      color: #fff !important;
      font-family: roboto;
      position: relative;
      display: -webkit-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      -webkit-flex-flow: column;
      -ms-flex-flow: column;
      flex-flow: column;
      box-shadow: 0px 15px 50px 10px rgba(0, 0, 0, 0.2);
      box-sizing: border-box;
      height: 600px;
      border-radius: 8px;
      width: 360px;
      background: rgb(2, 0, 36);
      background: linear-gradient(
        0deg,
        rgba(2, 0, 36, 0.8186624991793592) 0%,
        rgba(121, 9, 121, 0.5833683815323004) 35%,
        rgba(0, 212, 255, 0.7346288857339811) 100%
      );
    }
    input {
      outline: none;
      border: none;
    }

    .overlay {
      background: -webkit-linear-gradient(#8ca6db, #b993d6);
      background: linear-gradient(#8ca6db, #b993d6);
      opacity: 0.85;
      filter: alpha(opacity=85);
      height: 560px;
      position: absolute;
      width: 360px;
      z-index: 1;
    }

    .bold-line {
      background: #e7e7e7;
      position: absolute;
      top: 0px;
      bottom: 0px;
      margin: auto;
      width: 100%;
      height: 360px;
      z-index: 1;
      opacity: 0.1;
      background: url("https://pexels.imgix.net/photos/27718/pexels-photo-27718.jpg?fit=crop&w=1280&h=823")
        left no-repeat;
      background-size: cover;
    }

    @media (max-width: 500px) {
      .container_register_form_1 {
        width: 100%;
        height: 100%;
      }
      .overlay {
        width: 100%;
        height: 100%;
      }
    }
  }
}
